import { Trans } from "react-i18next";
import "./About1.css";

export const About1 = (props) => {
    return (
      <div className="container about" id='#about1' >
        <h2 style={{textAlign:'center',  marginTop:'1vw'}}>
          {props.t(' ')}
        </h2>
        <div className="container-fluid backGr">
          <div className="row">
              <div className="col-md-6 backFon" style={{color:'white'}}>
                  <h1 className="h1 mb-lg-5">{props.t('')}:<br/> {props.t(' ,  , -')}</h1>
                  <p className="lead">{props.t('         .')}</p>
                  <p className="lead">{props.t('   , :')}</p>
                  <ul className="lead">
                      <li>{props.t(' ')}</li>
                      <li>{props.t('')}</li>
                      <li>{props.t(' ')}</li>
                      <li>{props.t('')}</li>
                      <li>{props.t('')}</li>
                      <li>{props.t('  .')}</li>
                  </ul>
                  <p>{props.t('        ,            .')}</p>
              </div>
            </div>
        </div>
        <div className="container-fluid backGr">
          <div className="row">
              <div className="col-md-6 backFon" style={{color:'white'}}>  
              </div>
              <div className="col-md-6 backFon" style={{color:'white'}}>
              <h1 className="h1 mb-lg-5">{props.t('     ')}</h1>
                  <p className="lead">
                    <Trans>
                      {props.t('  :    <br/> ,       <br/>   .<br/> <br/>      , <br/>      <br/>')} 
                      <br/>
                      ISO9001.
                    </Trans>
                    
                  </p>
                  <div className="btn btn-custom btn-lg page-scroll" style={{marginBottom:'2vw'}}>{props.t('')} ISO9001</div>
              </div>
            </div>
        </div>
      </div>
    )
  }




  
